/* Base styles for all content */
div {
    text-align: center;
  }
  
  .main {
    margin-bottom: 40px;
  }
  
  ol {
    text-align: left;
  }
  
  nav .nav-wrapper {
    background-color: #ffb8bd;
  }
  
  i {
    margin-right: 20px;
    color: black;
  }
  
  /* Dark Mode Styles */
  .dark-mode {
    background-color: #424242;
    color: white; /* font */
  }
  
  .dark-mode nav .nav-wrapper {
    background-color: #ffb8bd;
  }
  
  .dark-mode i {
    color: white;
    -webkit-transition: -webkit-filter 0.3s ease;
    transition: -webkit-filter 0.3s ease;
    -o-transition: filter 0.3s ease;
    transition: filter 0.3s ease;
    transition: filter 0.3s ease, -webkit-filter 0.3s ease; /* Smooth transition */

  }
  

  
  /* Allow line breaks in MaterializeCSS tooltips */
  .material-tooltip {
    white-space: pre-line;
    max-width: 400px;
  }


